import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addProduct } from "../../redux/slices/cartSlice";

function PizzaBlock({ id, title, price, imageUrl, components, sizes, types }) {
  const [activeType, setActiveType] = useState(0);
  const [activeSize, setActiveSize] = useState(0);

  const [size, setSize] = useState(sizes[0]);

  const typeName = ["Гарантія 1 рік", "Гарантія 2 роки"];

  const dispatch = useDispatch();

  const cartItem = useSelector((state) =>
    state.cart.items.find(
      (obg) =>
        obg.id === id && obg.type === typeName[activeType] && obg.size === size
    )
  );
  const addCount = cartItem ? cartItem.count : 0;

  const onClickAdd = () => {
    const item = {
      id,
      title,
      price,
      imageUrl,
      type: typeName[activeType],
      size: size,
      count: 1,
    };
    dispatch(addProduct(item));
  };

  const onClickActiveSize = (index, size) => {
    setActiveSize(index);
    setSize(size);
  };

  return (
    <div class="pizza-block">
      <img class="pizza-block__image" src={imageUrl} alt="Pizza" />
      <h4 class="pizza-block__title">{title}</h4>
      <p className="pizza-block__components">{components}</p>
      <div class="pizza-block__selector">
        <ul>
          {types.map((typeId) => (
            <li
              key={typeId}
              className={activeType === typeId ? "active" : " "}
              onClick={() => setActiveType(typeId)}
            >
              {typeName[typeId]}
            </li>
          ))}
        </ul>
        <ul>
          {sizes.map((size, index) => (
            <li
              key={index}
              className={activeSize === index ? "active" : " "}
              onClick={() => onClickActiveSize(index, size)}
            >
              {size}
            </li>
          ))}
        </ul>
      </div>

      <div class="pizza-block__bottom">
        <div class="pizza-block__price">{price} ₴</div>
        <button class="button button--outline button--add" onClick={onClickAdd}>
          <svg
            width="12"
            height="12"
            viewBox="0 0 12 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
              fill="white"
            />
          </svg>
          <span>Добавить</span>
          {addCount > 0 && <i>{addCount}</i>}
        </button>
      </div>
    </div>
  );
}

export default PizzaBlock;
