import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  totalPrice: 0,
  totalItems: 0,
  items: [],
};

const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    addProduct(state, action) {
      const findItem = state.items.find(
        (obg) =>
          obg.id === action.payload.id &&
          obg.type === action.payload.type &&
          obg.size === action.payload.size
      );

      if (findItem) {
        findItem.count++;
      } else
        state.items.push({
          ...action.payload,
          count: 1,
        });

      state.totalPrice = state.items.reduce((sum, obg) => {
        return obg.price * obg.count + sum;
      }, 0);

      state.totalItems = state.items.reduce((count, obg) => {
        return obg.count + count;
      }, 0);
    },
    minusProduct(state, action) {
      const findItem = state.items.find(
        (obg) =>
          obg.id === action.payload.id &&
          obg.type === action.payload.type &&
          obg.size === action.payload.size
      );

      if (findItem.count > 1) {
        findItem.count--;
      }

      state.totalPrice = state.items.reduce((sum, obg) => {
        return obg.price * obg.count + sum;
      }, 0);

      state.totalItems = state.items.reduce((count, obg) => {
        return obg.count + count;
      }, 0);
    },
    removeProduct(state, action) {
      const findItem = state.items.find(
        (obg) =>
          obg.id === action.payload.id &&
          obg.type === action.payload.type &&
          obg.size === action.payload.size
      );

      state.items = state.items.filter((obg) => obg !== findItem);

      state.totalPrice = state.items.reduce((sum, obg) => {
        return obg.price * obg.count + sum;
      }, 0);

      state.totalItems = state.items.reduce((count, obg) => {
        return obg.count + count;
      }, 0);
    },
    clearProduct(state) {
      state.items = [];
      state.totalPrice = 0;
      state.totalItems = 0;
    },
  },
});

export const { addProduct, removeProduct, minusProduct, clearProduct } =
  cartSlice.actions;

export default cartSlice.reducer;
