import React, { useCallback, useContext, useState } from "react";
import { SearchContext } from "../../App";
import styles from "./Search.module.scss";
import debounce from "lodash.debounce";

const Search = () => {
  const { searchValue, setSearchValue } = useContext(SearchContext);
  const [value, setValue] = useState("");

  const updateSearchValue = useCallback(
    debounce((str) => {
      setSearchValue(str);
    }, 750),
    []
  );

  const clearValue = () => {
    setValue("");
    setSearchValue("");
  };

  const onChangeInput = (e) => {
    setValue(e.target.value);
    updateSearchValue(e.target.value);
  };

  return (
    <div className={styles.search}>
      <input
        value={value}
        onChange={onChangeInput}
        className={styles.root}
        placeholder="Пошук комп'ютера..."
      />
      {searchValue && (
        <svg
          onClick={clearValue}
          id="Icons"
          className={styles.clearIcon}
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <defs></defs>
          <path d="M12,0A12,12,0,1,0,24,12,12.013,12.013,0,0,0,12,0Zm0,22A10,10,0,1,1,22,12,10.011,10.011,0,0,1,12,22Z" />
          <path
            class="cls-1"
            d="M16.707,7.293a1,1,0,0,0-1.414,0L12,10.586,8.707,7.293A1,1,0,1,0,7.293,8.707L10.586,12,7.293,15.293a1,1,0,1,0,1.414,1.414L12,13.414l3.293,3.293a1,1,0,0,0,1.414-1.414L13.414,12l3.293-3.293A1,1,0,0,0,16.707,7.293Z"
          />
        </svg>
      )}
    </div>
  );
};

export default Search;
