import React, { useEffect, useRef, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { setSortType } from "../redux/slices/filterSlice";

export const list = [
	{ name: "популярності \u2191", sortProperty: "-rating" },
  { name: "популярності \u2193", sortProperty: "rating" },
  { name: "ціні \u2191", sortProperty: "-price" },
  { name: "ціні \u2193", sortProperty: "price" },
  { name: "алфавіту \u2191", sortProperty: "-title" },
  { name: "алфавіту \u2193", sortProperty: "title" },
];

function Sort() {
  const dispatch = useDispatch();

  const sortType = useSelector((state) => state.filter.sort);
  const sortRef = useRef();

  const [open, setOpen] = useState(false);

  const onClickListItem = (obg) => {
    dispatch(setSortType(obg));
    setOpen(false);
  };

	useEffect(() => {
		const handleClickOutside = (event) => {
      const path = event.composedPath().includes(sortRef.current);
      if (!path) setOpen(false);
    };

    document.body.addEventListener("click", handleClickOutside);

    return () => document.body.removeEventListener("click", handleClickOutside);
  }, []);

  return (
    <div ref={sortRef} class="sort">
      <div class="sort__label">
        <svg
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b>Сортування по:</b>
        <span onClick={() => setOpen(!open)}>{sortType.name}</span>
      </div>
      {open && (
        <div class="sort__popup">
          <ul>
            {list.map((obg, i) => (
              <li
                key={i}
                className={
                  sortType.sortProperty === obg.sortProperty ? "active" : " "
                }
                onClick={() => onClickListItem(obg)}
              >
                {obg.name}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
}

export default Sort;
